.RestaurantAddressCard__wrapper-card{
    width: 99%;
    height: 20%;
    margin: 10px 0;
    background-color: #ffffff; 
    border: 1px solid #7f7a7a;
    border-radius: 15px;
 
    position: relative;
    display: flex;
    justify-content: end;
}
.RestaurantAddressCard__wrapper-card_click{
    background-color: #b8e387;
}
.RestaurantAddressCard__wrapper{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
} 
.RestaurantAddressCard__wrapper-text-block{
    margin: 0 auto;
}
.RestaurantAddressCard__h2{
    width: auto;
    font-size: 19px; 
    font-weight: 600;
}
.RestaurantAddressCard__wrapper-flex{
    margin-top: 20px;
    
    display: flex;
    justify-content: space-between;
}
.RestaurantAddressCard__wrapper-flex-p{
    display: flex;
    flex-direction: column;
    text-align: start;
}
.RestaurantAddressCard__wrapper-flex-p1{
    padding: 0 15px 0 30px;
}
.RestaurantAddressCard__wrapper-flex-p2{
    padding: 0 30px 0 15px;
}
.RestaurantAddressCard__p{
    font-size: 16px;
}
.RestaurantAddressCard__p-green-color{
    color: #1e9816;
}
.RestaurantAddressCard__wrapper-img{
    display: flex;
    align-items: center;
    height: 100%;
}
.RestaurantAddressCard__img{
    width: 45px;
    height: 45px;
}
@media only screen and (max-width: 1200px){
    .RestaurantAddressCard__img{
        width: 35px;
        height: 35px;
    }
}
@media only screen and (max-width: 992px){
    .RestaurantAddressCard__wrapper-card{
        height: 20%;
        justify-content: center;
    }
    .RestaurantAddressCard__img{
        width: 35px;
        height: 35px;
    }
    .RestaurantAddressCard__wrapper-img{
        display: flex;
        align-items: center;
        height: 100%;
    }
}
@media only screen and (max-width: 768px){
    .RestaurantAddressCard__img{
        width: 25px;
        height: 25px;
    }
    .RestaurantAddressCard__h2{
        font-size: 17px;
    }
    .RestaurantAddressCard__p{
        font-size: 14px;
    }
    .RestaurantAddressCard__wrapper-flex-p1{
        padding: 0 15px 0 10px;
    }
    .RestaurantAddressCard__wrapper-flex-p2{
        padding: 0 10px 0 15px;
    }
}
@media only screen and (max-width: 668px){
    .RestaurantAddressCard__h2{
        font-size: 18px;
    }
    .RestaurantAddressCard__p{
        font-size: 16px;
    }
    .RestaurantAddressCard__wrapper-card{
        justify-content: end;
    }
}
@media only screen and (max-width: 568px){
    .RestaurantAddressCard__h2{
        width: 300px;
        font-size: 15px;
    }
    .RestaurantAddressCard__p{
        font-size: 13px;
    }
}
@media only screen and (max-width: 480px){
    .RestaurantAddressCard__wrapper-card{
        width: 99%;
        height: 17%;
    }
    .RestaurantAddressCard__h2{
        width: 220px;
        font-size: 12px;
    }
    .RestaurantAddressCard__p{
        font-size: 10px;
    }
    .RestaurantAddressCard__wrapper-card{
        justify-content: end;
    }
    .RestaurantAddressCard__wrapper-flex{
        margin-top: 10px;
    }
    .RestaurantAddressCard__img{
        width: 25px;
        height: 25px;
    }
}
