.CardInput__flex-container{
    display:flex;
}
.CardInput__container-Img{
    padding: 20px;
    width: 150px;
    height: 100px;
    display: flex;
    justify-content: center;
}
.CardInput__container{
    /* padding-top: 30px; */
    width: 100%;
    margin: 0 auto;
    position: relative;
}
.CardInput__container-title{
    margin-bottom: 2px;
    padding: 0 10px;
    margin-top: 10px;
}
.CardInput__container-price{
    width: 100%;
    position: absolute;
    margin: 0 auto 15px;
    bottom: 0;
    color: green;
}

