}
@media only screen and (max-width: 992px){
    
}
@media only screen and (max-width: 768px){
    .CardInput__container-Img{
        width: 120px;
        height: 80px;
    }
    .CardInput__container-title, .CardInput__container-price{
        font-size: 16px;
    }
} 

@media only screen and (max-width: 480px){
    .CardInput__container-Img{
        width: 120px;
        height: 80px;
    }
    .CardInput__container-title, .CardInput__container-price{
        font-size: 15px;
    }
}
