.SaladsMenu__wrapper-card_flex{
    width: 31%;
}
.SaladsMenu__wrapper-card{
    width: 100%;
    height: 370px;
    margin: 10px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 0 1px rgba(0,0,0,.5);
    position: relative;
}
.SaladsMenu__container-img{
    padding: 30px 0 10px; 
    width: 150px;
    height: 150px; 
    margin: 0 auto;
}
.SaladsMenu__container_flex{
    width: 100%;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 0;
    margin-bottom: 30px;
}
.SaladsMenu__container-title{
    padding: 0 20px;
    font-weight: normal;
    letter-spacing: 1px;
}
.SaladsMenu__h2-color-green{
    color: green;
    display: flex;
    align-items: center;
    height: 100%;
}
.SaladsMenu__wrapper-button{
    height: 30px;
    display: flex;
    align-items: center;
    bottom: 0;
}
.SaladsMenu__button{
    width: 100px;
    height: 30px;
    font-size: 18px;
    background-color: #8df26e;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    margin-right: 50px;
}
.SaladsMenu__button:hover{
    background-color: #43d317;
}

@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    .SaladsMenu__wrapper-card{
        width: 100%;
        height: 350px;
        margin: 10px 5px;
    }
    .SaladsMenu__wrapper-card_flex{
        width: 48%;
    }
}
@media only screen and (max-width: 768px){
    .SaladsMenu__wrapper-card{
        width: 100%;
        height: 270px;
        margin: 10px 0px;
    }
    .SaladsMenu__wrapper-card_flex{
        width: 47%;
    }
    .SaladsMenu__container-img{
        width: 100px;
        height: 100px;
    }
    .SaladsMenu__container-title{
        font-size: 18px;
    }
    .SaladsMenu__h2-color-green{
        font-size: 19px;
    }
    .SaladsMenu__button{
        margin-right: 20px;
        font-size: 16px;
    }
    .SaladsMenu__container_flex{
        margin-bottom: 15px;
    }
}

@media only screen and (max-width: 480px){
    .SaladsMenu__container-img{
        width: 70px;
        height: 70px;
    }
    .SaladsMenu__wrapper-card{
        height: 200px;
        border-radius: 15px;
    }
    .SaladsMenu__container-title{
        font-size: 12px;
    }
    .SaladsMenu__h2-color-green{
        font-size: 13px;
    }
    .SaladsMenu__button{
        font-size: 13px;
        width: 70px;
        height: 20px;
        margin-right: 15px;
        border-radius: 7px;
    }
    .SaladsMenu__wrapper-button{
        height: 20px;
    }
    .SaladsMenu__container_flex{
        margin-bottom: 13px;
    }
}
