.Cart__wrapper{
    width: 100%;
    min-height: 100vh; 
    height: auto;
    background-color: rgb(252,244,211);
}

.Cart__h3-arrow{
    font-size: 25px;
}
.Cart__wrapper-card{
    padding-top: 100px;
    width: 55%;
    height: 500px;
    margin: 0 auto;
}
.Cart__wrapper-title_flex{
    display: flex;
    justify-content: space-between;
    height: 30px;
}
.Cart__wrapper1{
    width: 60%;
    display:flex;
    padding: 0 20px;
}
.Cart__wrapper2{
    width: 25%;
}
.Cart__wrapper3{
    width: 20%;
}
.Cart__wrapper-card-card{
    height: 70%;
    overflow-y: auto;
    border: 1px solid;
}
.Cart__wrapper-card-card::-webkit-scrollbar{
    width: 0;
}
.Cart__wrapper_flex{
    display:flex;
    height: 30px;
    justify-content: space-between;
    align-items: center;
}
.Cart__wrapper4{
    width: 80%;
    display: flex;
    justify-content: end;
    padding: 0 0 0 40px;
}
.Cart__wrapper5{
    width: 25%;
    display: flex;
    justify-content: center;
}
.Cart__wrapper-h1{
    width: 100%;
    height: calc(100vh - 110px);

    display: flex;
    justify-content: center;
    align-items: center;
}
.Cart__h1{
    
}
.Cart__h3{
    font-weight: normal;
}
.Cart__wrapper-button{
    margin-top: 30px;
}
.Cart__button{
    width: 150px;
    height: 40px;
    border: 0;
    border-radius: 10px;
    padding: 5px 10px;
    font-size: 20px;
    color: #ffffff;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    background-color: #7a482a;
}
.Cart__button:hover{
    background-color: #6e3f2b;
}
@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    .Cart__wrapper-card{
        width: 75%;
    }
}
@media only screen and (max-width: 768px){
    .Cart__wrapper-card{
        width: 85%;
    }
    .Cart__wrapper0{
        font-size: 15px;
    }
    .Cart__wrapper1{
        font-size: 15px;
        width: 40%;
    }
    .Cart__wrapper2{ 
        font-size: 15px;
    }
    .Cart__wrapper3{
        font-size: 15px;
    }
}
@media only screen and (max-width: 480px){
    .Cart__wrapper-card{
        width: 95%;
        height: 400px;
    }
    .Cart__wrapper1{
        padding: 0 5px;
    }
    .Cart__wrapper4{ 
        font-size: 14px;
    }
    .Cart__wrapper5{
        padding: 0 5px;
        font-size: 14px;
    }
    .Cart__wrapper-button{
        margin-top: 10px;
    }
    .Cart__button{
        width: 120px;
        height: 30px;
        font-size: 18px;
    }
    .Cart__wrapper-card{
        padding-top: 40px;
    }
    .Cart__h1{
        font-size: 25px;
    }
}
