.mainPage__wrapper {
  background-color: rgba(247, 228, 146, 0.4);
  position: absolute;
  margin: 0 auto;
  width: 100%;
  height: auto;
  min-height: 100vh;
  top: 0;
  z-index: -1;
}
.mainPage__nav_menu {
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainPage__logo {
  margin: 20px;
  width: 70px;
  height: 70px;
}
.mainPage__address-title {
  font-size: 19px;
}
.mainPage__logoBurger {
  margin: 30px 0 10px;
  width: 150px;
  height: 150px;
}
.mainPage__logoImg {
  margin: 20px;
  width: 150px;
  height: 150px;
}
.mainPage__logoImgPotato {
  margin: 20px;
  width: 250px;
  height: 150px;
}
.mainPage__address-edite-pencil {
  width: 40px;
  height: 40px;
}

.mainPage__input-menu {
  height: 30px;
  width: 350px;
  margin: 20px 20px 10px;
  padding: 5px 10px;
  font-size: 18px;
  border: 0;
  border-radius: 15px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  z-index: 3;
}
.mainPage__container-registration_flex {
  margin-left: 10px;
  padding-top: 10px;
  display: flex;
  align-items: center;
}
.mainPage__img_userCircle {
  width: 50px;
  height: 50px;
}
.mainPage__button-registration {
  width: 100px;
  height: 30px;
  margin-left: 5px;
  margin-right: 10px;
  border: 0;
  border-radius: 15px;
  font-size: 18px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  background-color: rgb(249, 235, 190);
}
.mainPage__button-registration-active {
  width: auto;
  height: 30px;
  margin-left: 5px;
  margin-right: 10px;
  border: 0;
  font-size: 18px;
  background-color: rgb(252, 244, 211);
}
.mainPage__button-registration:hover {
  background-color: #7a482a;
  color: #ffffff;
}
.mainPage__img_cart {
  width: 35px;
  height: 35px;
}
.mainPage__button-info {
  width: 150px;
  height: 30px;
  margin-right: 10px;
  margin-top: 10px;
  border: 0;
  border-radius: 15px;
  font-size: 18px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  background-color: rgb(249, 235, 190);
}
.mainPage__button-info:hover {
  background-color: #7a482a;
  color: #ffffff;
}
.mainPage__menu_line {
  width: 100%;
  height: 2px;
  border: 0;
  background-color: rgb(154, 154, 154);
}
.mainPage__menu-categories {
  margin: 10px 0;
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainPage__menu-categories-block1 {
  display: flex;
}
.mainPage__menu-categories-block2 {
  display: flex;
}
.mainPage__menu_h2 {
  padding: 5px 10px;
  margin: 0 5px;
  font-weight: 500;
}
.mainPage__menu_h2:hover {
  background-color: #fff;
  border-bottom: 3px solid green;
}
.mainPage__line_active {
  border-bottom: 3px solid green;
}
.mainPage__wrapper-menu {
  width: 80%;
  height: auto;
  margin: 20px auto;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  padding-bottom: 70px;
}
.mainPage__line {
  width: 90%;
  height: 3px;
  background-color: rgb(154, 154, 154); /*#d25e2d*/
  /* margin: 0 auto; */
  margin-bottom: 50px;
  position: absolute;
  bottom:0;
  left: 5%;
}
.mainPage__container-count-product {
  background-color: red;
  color: #ffffff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainPage__count-product {
  font-size: 15px;
}
.mainPage__container-burgerMenu {
  padding: 0 15px 0 5px;
  width: 35px;
  height: 40px;
  display: none;
}
.mainPage__img-burgerMenu {
  width: 100%;
  height: 100%;
}
.mainPage__h2-more-categories {
  padding: 5px 10px;
  margin: 0 5px;
  color: red;
  display: none;
}
.mainPage__menu-categories-block2.changed {
  display: flex;
  justify-content: flex;
}
@media only screen and (max-width: 1200px) {
}
@media only screen and (max-width: 992px) {
  .mainPage__container-burgerMenu {
    margin: 10px 0 0;
  }
  .mainPage__img_cart {
    margin-top: 10px;
  }
  .mainPage__logo {
    margin: 20px 5px;
    width: 50px;
    height: 50px;
  }
  .mainPage__address-title,
  .mainPage__address-edite-pencil,
  .mainPage__container-registration_flex,
  .mainPage__button-info {
    display: none;
  }
  .mainPage__container-burgerMenu,
  .mainPage__h2-more-categories {
    display: block;
  }
  .mainPage__menu_h2,
  .mainPage__h2-more-categories {
    font-size: 20px;
  }
  .mainPage__menu-categories-block1 {
    display: flex;
  }
  .mainPage__menu-categories-block2 {
    display: none;
  }
  .mainPage__menu-categories {
    width: 100%;
    margin: 0 auto;
    display: block;
  }
  .mainPage__container_flex {
    display: flex;
    justify-content: center;
  }
}
@media only screen and (max-width: 768px) {
  .mainPage__container-burgerMenu {
    margin: 0;
  }
  .mainPage__img_cart {
    margin-top: 0;
  }
  .Container__wrapper-arrow {
    display: none;
  }
  .mainPage__input-menu {
    width: 230px;
    height: 27px;
    font-size: 15px;
    margin: 20px 10px 20px;
  }
  .mainPage__menu_h2 {
    font-size: 14px;
  }
  .mainPage__h2-more-categories {
    font-size: 14px;
  }
  .mainPage__logo {
    width: 40px;
    height: 40px;
  }
}
@media only screen and (max-width: 480px) {
  .mainPage__container-burgerMenu,
  .mainPage__img_cart {
    width: 25px;
    height: 25px;
  }
  .mainPage__logo {
    width: 35px;
    height: 35px;
  }
  .mainPage__input-menu {
    width: 150px;
    font-size: 11px;
    height: 23px;
    margin: 20px 5px;
  }
  .mainPage__menu_h2 {
    font-size: 12px;
    margin: 0;
    padding: 5px 5px;
  }
  .mainPage__h2-more-categories {
    font-size: 12px;
    margin: 0;
  }
  .mainPage__container-burgerMenu{
    padding: 0 5px 0 5px;
  }
  .mainPage__container-count-product {
    width: 15px;
    height: 15px;
  }
  .mainPage__count-product {
    font-size: 11px;
  }
  .mainPage__wrapper-menu{
    width: 90%;
    margin: 10px auto;
  }
}
@media only screen and (max-width: 320px) {
}
