.AddInCart__blur{
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    backdrop-filter: blur(4px);
    background-color: rgba(247, 228, 146, .4); 
}
.AddInCart__container-wrapper-window{
    height: auto;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
}
.AddInCart__wrapper-window{
    width: 700px;
    height: 500px;
    background-color: #efc39a;
    border-radius: 15px;
    
    top: 50%;
    transform: translateY(-50%);
    position: fixed;
}
.AddInCart__container-img-cross{
    display: flex;
    justify-content: end;
}
.AddInCart__img-cross{
    padding: 10px;
    width: 45px;
    height: 45px;
    position: relative;
}
.AddInCart__h2-title{
    font-size: 30px;
}
.AddInCart__wrapper-h3-calories{
    display: flex;
    justify-content: start;
    margin-left: 10%;
}
.AddInCart__img-Arrow{
    padding-top: 15px;
    width: 35px;
    height: 35px;
}
.AddInCart__h3-calories{
    margin-top: 20px;
    font-weight: normal;
}
.AddInCart__wrapper-scroll{
    height: 250px;
    margin-bottom: 10px;
    overflow-y: auto;
    scroll-snap-type: y mandatory;

    display: flex;
    align-items: center;
}
.AddInCart__wrapper-scroll::-webkit-scrollbar {
    width: 0;
}
.AddInCart__wrapper-button-masses{
    width: 130px;
    height: 30px;
    margin: auto;
    margin-top: 20px;
    border: 2px solid #29292966;

    border-radius: 20px;

    display: flex;
    justify-content: center;
    align-items: center;
}
.AddInCart__wrapper-img{
    width: 90%;
    height: 90%;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 30px;

}
.AddInCart__wrapper-flex{
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
.AddInCart__flex-container-button{
    width: 150px;
    height: 50px;
    
    border-radius: 70px;
    border: 2px solid #29292966;

    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 10px;
    font-size: 25px;
}
.AddInCart__button-AddInCart{
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.AddInCart__wrapper_button-Add{
    display: flex;
    align-items: center;
}
.AddInCart__button-Add{
    width: 250px;
    height: 50px;
    border: 0;
    border-radius: 15px;
    font-size: 23px;
    color: #ffffff;
    background-color: #7a482a;
}
.AddInCart__button-Add:hover{
    background-color: #5a3526;
}
.AddInCart__img-plus-minus{
    border-radius: 50%;
    border: 2px solid #413e3e;
    width: 30px;
    height: 30px;
    background-color: #7a482a;
}
.AddInCart__title-calories{
    font-size: 18px;
}
.AddInCart__button-masses{
    height: 23px;
    padding: 0 10px;
    margin: 0 5px;
    border-radius: 15px;
    border: 0;
    font-size: 15px;
    background-color: #efc39a;
}
.AddInCart__button-masses-active{
    background-color: #7a482a;
    color: #ffffff;
}
.AddInCart__wrapper_flex{
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.AddInCart__wrapper-item{
    margin: 0 10px;
}

@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    
}
@media only screen and (max-width: 768px){
    .AddInCart__wrapper-window{
        width: 450px;
        height: 480px;
    }
    .AddInCart__wrapper-img{
        width: 70%;
        height: 70%;
    }
    .AddInCart__wrapper-scroll{
        height: 230px;
    }
} 

@media only screen and (max-width: 480px){
    .AddInCart__wrapper-window{
        width: 290px;
        height: 400px;
    }
    .AddInCart__wrapper-scroll{
        height: 150px;
        margin: 0;
    }
    .AddInCart__wrapper-img{
        width: 70%;
        height: 70%;
    }
    .AddInCart__h2-title{
        font-size: 18px;
    }
    .AddInCart__h3-calories{
        font-size: 15px;
    }
    .AddInCart__wrapper-flex{
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .AddInCart__flex-container-button{
        margin: 0 auto;
        width: 120px;
        height: 40px;
    }
    .AddInCart__wrapper_button-Add{
        margin: 10px auto 0;
    }
    .AddInCart__button-Add{
        width: 180px;
        height: 30px;
        font-size: 16px;
        border-radius: 10px;
    }
    .AddInCart__img-plus-minus{
        width: 25px;
        height: 25px;
    }
    .AddInCart__button-AddInCart{
        padding: 5px;
    }
    .AddInCart__title-calories{
        font-size: 12px;
    }
}
