.RestaurantBlock__background{
    height: 100vh;
    background-color: rgba(247, 228, 146, .4); 
}
.RestaurantBlock__wrapper_flex{
    display: flex;
    justify-content: space-between;
}
.RestaurantBlock__wrapper{
    width: 40%;
    height: 100vh;
    background-color: #efc39a;
    z-index: 100;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.RestaurantBlock__container-img{
    width: 100%;
    display: flex;
    justify-content: end;
}
.RestaurantBlock__img{
    padding: 10px 10px;
    width: 50px;
    height: 50px;
}
.RestaurantBlock__wrapper-card{
    width: 90%;
    height: 80%;
    margin: 0 auto;
    overflow-y: auto;
    z-index: 101;
}
.RestaurantBlock__wrapper-card::-webkit-scrollbar{
    width: 0;
}
.RestaurantBlock__button{
    width: 200px;
    height: 50px;
    margin: 20px;
    font-size: 20px;
    background-color: #8f8f8f;
    color: #ffffff;
    border-radius: 15px;
    border: 0;
    box-shadow: 0 0 5px rgba(0,0,0.5);
    margin-top: 20px;
}
.RestaurantBlock__button-active{
    width: 200px;
    height: 50px;
    margin: 20px;
    font-size: 20px;
    background-color: #7a482a;
    color: #ffffff;
    border-radius: 15px;
    border: 0;
    box-shadow: 0 0 5px rgba(0,0,0.5);
    margin-top: 20px;
}
.RestaurantBlock__container-img-background{
    width: 60%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.RestaurantBlock__img-background{
    width: 60%;
    height: 60%;

    top: 50%;
    transform: translateY(-50%);
    position: absolute;
}
.RestaurantBlock__container-cross{
    width: 100%;
    display: flex;
    justify-content: end;
}
@media only screen and (max-width: 1200px){
  
}
@media only screen and (max-width: 992px){
    .RestaurantBlock__wrapper{
        width: 60%;
        top: 0;
        left: 0;
        margin: 0 auto;
        border-radius: 0;
        background-color: #efc39a;
    }
    .RestaurantBlock__container-img{
        margin-top: -20px;
    }
    .RestaurantBlock__img-background{
        width: 40%;
        height: 40%;
    }
}
@media only screen and (max-width: 768px){
    .RestaurantBlock__container-img{
        margin-top: -5px;
    }
    .RestaurantBlock__img-background{
        display: none;
    }
    .RestaurantBlock__wrapper{
        width: 100%;
    }
    .RestaurantBlock__wrapper_flex{
        display: block;
    }
    .RestaurantBlock__container-cross{
        width: 140%;
        top: 0;
        position: absolute;
    }
}
@media only screen and (max-width: 480px){
    .RestaurantBlock__container-cross{
        width: 100%;
    }
    .RestaurantBlock__wrapper{
        padding-top: 20px;
    }
    .RestaurantBlock__container-img-background{
        width: 100%;
    }
    .RestaurantBlock__img{
        padding: 10px;
        width: 30px;
        height: 30px;
    }
    .RestaurantBlock__container-img{
        margin-top: -5px;
    }
    .RestaurantBlock__button{
        width: 100px;
        height: 35px;
        margin: 15px;
        font-size: 15px;
    }
    .RestaurantBlock__button-active{
        width: 100px;
        height: 35px;
        margin: 15px;
        font-size: 15px;
    }
}
