.ModulWindow__container-wrapper{
    height: 100vh;
    width: 100%;
    /* margin: 0 auto; */

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
}
.ModulWindow__wrapper{
    width: 750px;
    height: 470px;
    background-color: #efc39a;
    border-radius: 20px;
    position: fixed;
    z-index: 100;
}
.ModulWindow__title_h3{
    font-size: 30px;
    padding: 0px 50px 2px;
    text-align: center;
}
.ModulWindow__title_p{
    font-size: 27px;
    text-align: center;
}
.ModulWindow__flex-container{
    display: flex;
    justify-content: center;
}
.ModulWindow__img{
    margin-top: 30px;
    width: 100px;
    height: 100px;
}
.ModulWindow__title_h4{
    margin-top: 10px;
    font-size: 20px;
}
.ModulWindow__card{
    width: 200px;
    height: 200px;
    border-radius: 20px;
    margin:20px;
    border: 1px solid #7f7a7a;
    background-color: #f8ebd8;
    text-align: center;
}
.ModulWindow__card_active{
    width: 200px;
    height: 200px;
    border-radius: 20px;
    margin:20px;
    border: 1px solid #7f7a7a;
    background-color: #ffffff;
    text-align: center;
}
.ModulWindow__container-img_check-tick{
    display:flex;
    justify-content: flex-end;
}
.ModulWindow__img_check-tick{
    position:absolute;
    padding: 10px;
    width: 25px;
    height: 25px;
}

@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    .ModulWindow__wrapper{
        width: 570px;
        height: 460px;
    }
    .ModulWindow__title_h3{
        font-size: 25px;
    }
    .ModulWindow__title_p{
        font-size: 22px;
    }
}
@media only screen and (max-width: 768px){
    .ModulWindow__wrapper{
        width: 450px;
        height: 390px;
    }
    .ModulWindow__title_h3{
        font-size: 19px;
    }
    .ModulWindow__title_p{
        font-size: 17px;
    }
    .ModulWindow__card{
        width: 150px;
        height: 150px;
    }
    .ModulWindow__card_active{
        width: 150px;
        height: 150px;
    }
    .ModulWindow__img{
        margin-top: 20px;
        width: 70px;
        height: 70px;
    }
    .ModulWindow__title_h4{
        font-size: 17px;
    }
}
@media only screen and (max-width: 480px){
    .ModulWindow__wrapper{
        width: 300px;
        height: 290px;
    }
    .ModulWindow__title_h3{
        font-size: 14px;
        padding: 0 20px 2px;
    }
    .ModulWindow__title_p{
        padding: 0 20px;
        font-size: 12px;
    }
    .ModulWindow__card{
        width: 110px;
        height: 110px;
        margin: 10px;
    }
    .ModulWindow__card_active{
        width: 110px;
        height: 110px;
        margin: 10px 10px;
    }
    .ModulWindow__img{
        margin-top: 20px;
        width: 50px;
        height: 50px;
    }
    .ModulWindow__title_h4{
        font-size: 12px;
    }
    .ModulWindow__img_check-tick{
        position:absolute;
        padding: 7px;
        width: 18px;
        height: 18px;
    }
}
