.WindowRecherche__blur{
    width: 100%;
    height: 100vh;
    top:0;
    left:0;
    /* background-color: rgba(0,0,0,.5); */
    position: fixed;
    z-index: 1;
}
.WindowRecherche__container-input{
    position: absolute;
    width: 370px;
    height: 300px;
    margin: 0 20px;
    background-color: #ffffff;
    border: 1px solid;
    border-radius: 10px;
    overflow-y: auto;
    z-index: 1;
}

.WindowRecherche__container-input::-webkit-scrollbar{
    width: 0;
}
@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    
}
@media only screen and (max-width: 768px){
    .WindowRecherche__container-input{
        width: 250px;
        margin: -10px 10px;
    }
}

@media only screen and (max-width: 480px){
    .WindowRecherche__container-input{
        width: 220px;
        margin: -10px -20px;
    }
}
