.BurgerMenuWindow__blur{
    width: 100%;
    height: 100vh;
    top:0;
    left:0;
    /* background-color: rgba(0,0,0,.5); */
    position: fixed;
    z-index: 1;
}
.BurgerMenuWindow__container{
    margin-top: 80px;
    margin-left: 425px;
    position: absolute;
    width: 200px;
    height: auto;
    background-color: #ffffff;
    border: 1px solid;
    border-radius: 10px;
    top: 0;
    z-index: 2;
}
.BurgerMenuWindow__title{
    font-size: 22px;
    font-weight: normal;
    padding: 5px 0;
}

.BurgerMenuWindow__title1:hover{
    border-radius: 10px 10px 0 0;
    background-color: rgb(154, 154, 154);
}
.BurgerMenuWindow__title2:hover{
    background-color: rgb(154, 154, 154);
}
.BurgerMenuWindow__title3:hover{
    border-radius: 0 0 10px 10px;
    background-color: rgb(154, 154, 154);
}

