.PayWindow__blur{
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    backdrop-filter: blur(4px);
    background-color: rgba(247, 228, 146, .4); 
    z-index: -1;
}
.PayWindow__container-wrapper-widow{
    width: 100%;
    height: auto;

    display: flex;
    justify-content: center;
    align-items: center; 
}
.PayWindow__wrapper-widow{
    width: 700px;
    height: 500px;
    background-color: #efc39a;
    border-radius: 15px;

    position: fixed;
    top: 50%;
    transform: translateY(-50%);
}
.PayWindow__wrapper-h3{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: start;
}
.PayWindow__h3{
    font-weight: normal;
}
.PayWindow__wrapper-img-payCard{
    width: 60%;
    height: 60%;
    margin: 0 auto;
}
.PayWindow__img-payCard{
    width: 100%;
    height: 100%;
}
.PayWindow__container_flex{
    display: flex;
    justify-content: center;
}

.PayWindow__img-checkmark{
    width: 50px;
    height: 50px;
}
.PayWindow__h1{
    font-weight: normal;
}
@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    .PayWindow__wrapper-widow{
        width: 500px;
        height: 300px;
    }
    .PayWindow__container_flex{
        font-size: 11px;
    }
    .PayWindow__img-checkmark{
        width: 30px;
        height: 30px;
    }
    .PayWindow__wrapper-img-payCard{
        width: 50%;
        height: 50%;
    }
}
@media only screen and (max-width: 768px){
    .PayWindow__wrapper-widow{
        width: 470px;
        height: 300px;
    }
    .PayWindow__container_flex{
        font-size: 12px;
    }
    .PayWindow__img-checkmark{
        width: 30px;
        height: 30px;
    }
}
@media only screen and (max-width: 480px){
    .PayWindow__wrapper-widow{
        width: 270px;
        height: 250px;
    }
    .PayWindow__container_flex{
        font-size: 8px;
        padding: 0 10px;
    }
    .PayWindow__wrapper-img-checkmark{
        display: flex;
        align-items: center;
    }
    .PayWindow__img-checkmark{
        width: 20px;
        height: 20px;
    }
    .PayWindow__h3{
        font-size: 12px;
    }
    .PayWindow__wrapper-img-payCard{
        width: 60%;
        height: 60%;
    }
}
