.RegistrationBlock__wrapper-blure{
    width: 100%;
    height: 100vh;
    background-color: rgb(252,244,211);
     
}
.RegistrationBlock__wrapper_flex{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.RegistrationBlock__wrapper{
    width: 700px;
    height: 450px; 
    background-color: #efc39a;
    border-radius: 20px;
    z-index: 2;
}

.RegistrationBlock__h1{
    padding: 0 0 10px 0;
    font-size: 32px;
}
.RegistrationBlock__p{
    padding: 0 0 5px 0;
    font-size: 23px;
}
.RegistrationBlock__wrapper-input{
    margin: 10px;
}
.RegistrationBlock__input{
    width: 220px;
    height: 40px;
    padding: 0 10px;
    font-size: 20px;
    border: 1px solid #bfbfbf;
    border-radius:10px;
}
.RegistrationBlock__input-noActive{
    background-color: #e77474;
}
.RegistrationBlock__wrapper-line{
    padding-top: 10px;
    width: 80%;
    margin: 0 auto;
}
.RegistrationBlock__line{
    height: 2px;
    border: 0;
    background-color: #666666;
}
.RegistrationBlock__button{
    margin: 20px;
    width: 270px;
    height: 45px;
    background-color:#7a482a;
    color: #ffffff;
    border: 0;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    border-radius: 10px;
    font-size: 23px;
}
.RegistrationBlock__input-two{
    width: 250px;
    height: 45px;
    padding: 0 10px;
    font-size: 23px;
    border: 1px solid #bfbfbf;
    border-radius: 10px;
    margin: 7px;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
}
@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    
}
@media only screen and (max-width: 768px){
    .RegistrationBlock__wrapper{
        width: 100%;
        height: 100%;
        left:0;
        margin: 0;
        border: 0;
    }
    .RegistrationBlock__wrapper-text{
        margin: 0 auto;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
        position: absolute;
    }
    .RegistrationBlock__wrapper-blure{
        background-color: #efc39a;
    }
}
@media only screen and (max-width: 480px){
    .RegistrationBlock__button{
        width: 140px;
        height: 30px;
        font-size: 20px;
    }
    .RegistrationBlock__h1{
        font-size: 27px;
    }
    .RegistrationBlock__p{
        padding: 0 10px 5px;
        font-size: 15px;
    }
    .RegistrationBlock__input{
        width: 170px;
        height: 30px;
        font-size: 15px;
    }
    .RegistrationBlock__input-two{
        width: 100px;
        height: 30px;
        font-size: 15px;
    }
}
