.App {
  text-align: center;
}
.mainPage__logoImg{
  width: 100%;
  height: 100%;
}
.mainPage__logoBurger{
  width: 100%;
  height: 100%;
}
.mainPage__logoImgPotato{
  width: 100%;
  height: 100%;
}
.Container__wrapper-arrow{
  padding: 10px 20px;
  position: absolute;
  display:flex;
  align-items: center; 
}
.Container__img-arrow{
  width:70px;
  height:70px;
}
.container-img{
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.img{
  padding:5px;
  width:45px;
  height:45px;
}
.Container__h3-arrow{
  font-size: 25px;
}
