.ModulWindow__container-button{
    display: flex;
    justify-content: center;
}
.ModulWindow__button{
    margin-top: 10px;
    width: 270px;
    height: 50px;
    border: 1px solid #7f7a7a;
    border-radius: 15px;
    font-size: 18px;
    background-color: #7a482a;
    color: #eae6e6;
    box-shadow: 0 0 5px rgba(0, 0, 0, .3);
    
}
.ModulWindow__button:hover{
    background-color:#663414;
}
@media only screen and (max-width: 480px){
    .ModulWindow__button{
        width: 150px;
        height: 35px;
        font-size: 13px;
    }
}