.Info__wrapper{
    width: 100%;
    min-height: 100vh;
    height: auto;
    top: 0;
    left: 0;
    background-color:rgb(252,244,211);
    margin: 0 auto;
    padding-bottom: 100px;
}
.Info__wrapper-p{
    height: auto;
    min-height: 100%;
    width: 60%;
    margin: 0 auto;
    padding: 100px 20px 0;

    display: flex;
    justify-content: center;
    align-items: center;
}
.Info__p{
    margin: 0 auto;
    font-size: 23px;
    letter-spacing: 1px;
    line-height: 45px;
    text-align: justify;
}
@media only screen and (max-width: 1200px){
    .Info__wrapper-p{
        width: 70%;
    }
    .Info__p{
        font-size: 20px;
        letter-spacing: 1px;
        line-height: 45px;
    }
}
@media only screen and (max-width: 992px){
    .Info__wrapper-p{
        width: 70%;
    }
    .Info__p{
        font-size: 18px;
        letter-spacing: 0px;
        line-height: 30px;
    }
}
@media only screen and (max-width: 768px){
    .Info__wrapper-p{
        width: 70%;
    }
    .Info__p{
        font-size: 16px;
        letter-spacing: 0px;
        line-height: 25px;
    }
}
@media only screen and (max-width: 480px){
    .Info__wrapper-p{
        width: 80%;
        padding: 40px 20px 0;
    }
    .Info__p{
        font-size: 14px;
        letter-spacing: 0px;
        line-height: 22px;
    }
}
