.CardCart__wrapper-card{
    width: 100%; 
    height: 50px;
    margin: 0 auto;
    border: 1px solid #66666696;
}
.CardCart__wrapper-text_flex{
    display: flex;
    justify-content: space-between;
    
}
.CardCart__wrapper1{
    width:60%;
    height:50px;
    display:flex;
    align-items: center;
    text-align: left;
    padding: 0 20px;
}
.CardCart__wrapper2{
    width:25%;
    height:50px;
    display:flex;
    justify-content: center;
    align-items: center;
    
}
.CardCart__wrapper3{
    width:20%;
    height:50px;
    display:flex;
    justify-content: center;
    align-items: center;
    
}
.CardCart__h3{
    font-weight: normal;
}
@media only screen and (max-width: 1200px){
    
}
@media only screen and (max-width: 992px){
    
}
@media only screen and (max-width: 768px){
    .CardCart__wrapper1{
        width:40%;
    }
    .CardCart__h3{
        font-size: 15px;
    }
}
@media only screen and (max-width: 480px){
    .CardCart__wrapper1{
        padding: 0 5px;
    }
    .CardCart__h3{
        font-size: 14px;
    }
}
